export default {
  update(el, binding, vnode) {
    const vm = vnode.context;
    if (!vm.i18n) { return binding.value; }

    const placeholder = vm.$t ? vm.$t(binding.value) : binding.value;
    el.placeholder += placeholder;
  }
};



